.arrow-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    cursor: pointer;
}

.bouncing-arrow {
    width: 30px;
    height: 30px;
    border-left: 2px solid var(--light-accent-purple);
    border-bottom: 1px solid var(--light-accent-purple);
    transform: rotate(-45deg);
    animation: bounce 1.5s infinite;
    border-width: 1.4px;
}

.json-uploader.dark .bouncing-arrow {
    border-left-color: var(--dark-accent-purple);
    border-bottom-color: var(--dark-accent-purple);
}

@keyframes bounce {

    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0) rotate(-45deg);
    }

    40% {
        transform: translateY(-10px) rotate(-45deg);
    }

    60% {
        transform: translateY(-5px) rotate(-45deg);
    }
}